import React, { useState } from 'react';
import styled from 'styled-components';
import ThankyouMessage from '../Components/ThankyouMessage';

import kakaostamp from "../assets/kakao_stamp.png";
import githubstamp from "../assets/git_stamp.png";

export default ({tab}) => {
    const [rotate, setRotate] = useState(true);

    const handleRotate = (e) => {
        rotate ? setRotate(false) : setRotate(true);
    }
    return (
        <Background ref={tab}>
            <Container>
                <Title>CONTACT</Title>
                <Subtitle>발전하는 신입개발자가 필요하시다면 아래와 같은 방법으로 연락하실 수 있습니다.</Subtitle>
                <Postcard>
                    <PostcardFront rotate={rotate}>
                        <RotateBtn 
                            onClick={handleRotate}   
                            className="material-icons-outlined">
                            loop
                        </RotateBtn>
                        <div>
                            <CardTextTitle>보낸 사람</CardTextTitle>
                            <CardTextContent>
                                <span className="material-icons-outlined">email</span>
                                <span>이메일</span>
                                <span>gd693020@gmail.com</span>
                            </CardTextContent>
                            <CardTextContent>
                                <span className="material-icons-outlined">room</span>
                                <span>주소</span>
                                <span>경기도 의왕시</span>
                            </CardTextContent>
                            <CardTextContent>
                                <span className="material-icons-outlined">person</span>
                                <span>이름</span>
                                <span>김지용</span>
                            </CardTextContent>
                            <CardTextContent>
                                <span className="material-icons-outlined">call</span>
                                <span>연락처</span>
                                <span>010-3543-3261</span>
                            </CardTextContent>
                        </div>
                        <Stamps>
                            <Stamp href="https://github.com/JiyongInSpace" target="_blank">
                                <Img src={githubstamp}></Img>
                            </Stamp>
                            <Stamp href="https://open.kakao.com/o/sLed3HCd" target="_blank">
                                <Img src={kakaostamp}></Img>
                            </Stamp>
                        </Stamps>
                    </PostcardFront>
                    <Form
                        rotate={rotate}
                        className="gform"
                        method="POST"
                        action="https://script.google.com/macros/s/AKfycbzdHHW11UlA6Zllx6OQRgFjGMKBLlMIqpS1MGGcwXG3V4CrTGia1lndLMaG_NYjuilG/exec"
                    >
                        <RotateBtn 
                            onClick={handleRotate}   
                            className="material-icons-outlined">
                            loop
                        </RotateBtn>
                        <FormEl>
                            <Input type="text" placeholder="Enter Your Name" id="name" name="name" required></Input>
                            <Input type="mail" placeholder="Enter Your Email" id="email" name="email" required></Input>
                            <Textarea placeholder="Enter Your Message" id="message" name="message" rows="8" required></Textarea>
                            <Button>Send</Button>
                        </FormEl>
                        <ThankyouMessage />
                    </Form>
                </Postcard>
            </Container>
        </Background>
    );
}


const Background = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const Container = styled.article`
    width: 1340px;
`;
const Title = styled.h2`
    font-size: 4em;
    text-align: center;
    font-weight: 800;
`;
const Subtitle = styled.p`
    text-align: center;
    color: gray;
    margin: 50px 0;
`
const Postcard = styled.section`
    width: 750px; height: 450px;
    margin: 0 auto;
    position: relative;
    perspective: 2000px;
    &>div, &>form{
        backface-visibility: hidden;
        transition: 1s;
        box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.3);
    }
`;
const PostcardFront = styled.div`
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
    transform: ${props => props.rotate ? "rotateY(0deg)" : "rotateY(180deg)"};
    padding: 50px 60px;
    display: grid;
    grid-template-columns: 1fr 1fr;
`;
const CardTextTitle = styled.h4`
    font-size: 1.5em;
    color: blue;
    font-weight: 600;
    margin-bottom: 25px;
`;
const CardTextContent = styled.p`
    font-size: 1.2em;
    margin-bottom: 10px;
    display: grid;
    grid-template-columns: 60px 1fr;
    grid-template-rows: 1fr 1fr;
    &>span:nth-child(1){
        grid-column: 1 / 2; grid-row: 1 / 3;
        font-size: 2em;
        padding-top: 4px;
    }
    &>span:nth-child(2){
        padding: 7px 3px 3px 3px;
        font-weight: 600;
    }
    &>span:nth-child(3){
        margin-top: 7px;
    }
`;
const Stamps = styled.div`
    margin-left: auto;
    display: flex;
    align-items: flex-start;
    margin-top: -10px;
`;
const Stamp = styled.a`
    display: block;
    width: 90px; height: 120px;
    background-color: white;
    transition: 0.3s;
    &:hover{
        transform: scale(1.05);
    }
`;
const Img = styled.img`
    width: 100%; height: 100%;
    cursor: pointer;
`;


const Form = styled.form`
    width: 100%; height: 100%;
    transform: ${props => props.rotate ? "rotateY(-180deg)" : "rotateY(0deg)"};
`;
const RotateBtn = styled.span`
    position: absolute;
    top: 10px; right: 10px;
    cursor: pointer;
    color: gray;
    &:hover{
        transform: rotate(-180deg);
        transition: 1s;
    }
`;
const FormEl = styled.div`
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px;
`;
const Input = styled.input`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    &:focus{outline: none;}
`;
const Button = styled.button`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    cursor: pointer;
    border-right: 1px solid lightgray;
    transition: 0.4s;
    &:active{transform: scale(0.99);}
    &:hover{background-color: gray; color:white;}

`;

const Textarea = styled.textarea`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid lightgray;
    font-size: 20px;
    resize: none;
    &::-webkit-scrollbar{width:5px;}
    &::-webkit-scrollbar-thumb{background-color: white;}
    &::-webkit-scrollbar-track{background-color: silver;}
    &:focus{outline: none;}
`;